﻿import {Injectable} from '@angular/core';
import {HttpClient} from '@angular/common/http';

import {User} from '../_models';
import {map} from 'rxjs/operators';
import {environment} from '../../environments/environment';

@Injectable()
export class UserService {
  constructor(private http: HttpClient) {
  }

  getAll() {
    return this.http.get<any>(`${environment.apiUrl}/api/user`).pipe(map(res => {
      return res.data;
    }));
  }

  getById(id: number) {
    return this.http.get(`${environment.apiUrl}/api/user` + id);
  }

  register(user: User) {
    return this.http.post(`${environment.apiUrl}/api/user`, user);
  }

  update(user: User) {
    return this.http.put(`${environment.apiUrl}/api/user` + user.id, user);
  }

  delete(id: number) {
    return this.http.delete(`${environment.apiUrl}/api/user` + id);
  }
}
